import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    versionId: '',  //  版本id
    department: '',
    device: '', 
    organizationId: '', //  选择装置 需要用到的参数
    userId: '', //  登录成功返回的value 不知道是不是某些接口需要的用户id 先拼到这这里

    btnShow: null, //  底部按钮显示隐藏的状态
  },
  getters: {
    getVersionId (state) {
      return state.versionId
    },
    getDepartment (state) {
      return state.department
    },
    getDevice (state) {
      return state.device
    },
    getOrganizationId (state) {
      return state.organizationId
    },
    getUserId(state) {
      return state.userId;
    },
    getBtnShow(state) {
      return state.btnShow;
    }
  },
  mutations: {
    changeVersionId(state, val) {
      state.versionId = val;
      window.localStorage.setItem('versionId', val)
    },
    changeDepartment (state, val) {
      // debugger
      state.department = val
    },
    changeDevice (state, val) {
      state.device = val
    },
    changeOrganizationId(state, val) {
      state.organizationId = val
    },
    changeUserId(state, val) {
      state.userId = val
    },

    changeBtnShow(state, val) {
      console.log('changeBtnShow', val)
      state.btnShow = val;
    }
  },
  actions: {
  },
  modules: {
  }
})
