Vue.prototype.$getHttp = fetchGet
Vue.prototype.$postHttp = fetchPost
Vue.prototype.$putHttp = fetchPut
Vue.prototype.$baseUrl = 'http://10.10.10.67:11012' //  开发测试环境

// Vue.prototype.$baseUrl = 'http://124.70.56.237:11012' //  发版环境  

Vue.use(Vant)
Vue.config.productionTip = false


var curPage;
var isQuit;
router.beforeEach((to, from, next) => {
    // debugger
    console.log(to.name);
    curPage = to.path;
    isQuit = curPage == '/loadingScreen' || curPage == '/login' || curPage == '/version-election' || curPage == '/WorkTable' || curPage == '/wait-dealt' || curPage == '/mine' || curPage == '/update'
    next();
});

// debugger
// 监听手机物理返回
document.addEventListener('plusready', function() {
    // console.log('plusready--plusready', window.location.href, 'this.$route', this.$route.path)
    console.log('plusready--plusready', curPage)
  var webview = plus.webview.currentWebview();
  plus.key.addEventListener('backbutton', function() {
    // let curPage = window.location.href.split('#')[1] == '/loadingScreen' || window.location.href.split('#')[1] == '/login' || window.location.href.split('#')[1] == '/version-election' || window.location.href.split('#')[1] == '/WorkTable' || window.location.href.split('#')[1] == '/wait-dealt' || window.location.href.split('#')[1] == '/mine' || window.location.href.split('#')[1] == '/update'
    console.log('backbutton--backbutton', curPage, 'isQuit', isQuit)
      webview.canBack(function(e) {
        console.log('canBack', e)
          if(e.canBack) {
            if (!isQuit) {
                webview.back();
            } else {
                var first = null;
                plus.key.addEventListener('backbutton', function() {
                    //首次按键，提示‘再按一次退出应用’
                    if (!first && isQuit) {
                        first = new Date().getTime();
                        Toast('再按一次退出应用');  
                        console.log('再按一次退出应用1');
                        setTimeout(function() {
                            first = null;
                            console.log('定时器1')
                        }, 1000);
                    } else {
                        if (new Date().getTime() - first < 1500) {
                            plus.runtime.quit();
                        }
                    }
                }, false);
            }
          } else {
              //webview.close(); //hide,quit
              //plus.runtime.quit();
              //首页返回键处理
              //处理逻辑：1秒内，连续两次按返回键，则退出应用；
              var first = null;
              plus.key.addEventListener('backbutton', function() {
                  //首次按键，提示‘再按一次退出应用’
                  if (!first && isQuit) {
                      first = new Date().getTime();
                      Toast('再按一次退出应用');  
                      console.log('再按一次退出应用2');
                      setTimeout(function() {
                          first = null;
                          console.log('定时器2')
                      }, 1000);
                  } else {
                      if (new Date().getTime() - first < 1500) {
                          plus.runtime.quit();
                      }
                  }
              }, false);
          }
      })
  });
});

// 监听手机键盘弹出隐藏 (有问题)
// 这里区分不同系统
const ua = typeof window === 'object' ? window.navigator.userAgent : '';

let _isIOS     = -1;
let _isAndroid = -1;

function isIOS() {
  if (_isIOS === -1) {
    _isIOS = /iPhone|iPod|iPad/i.test(ua) ? 1 : 0;
  }
  return _isIOS === 1;
}

function isAndroid() {
  if (_isAndroid === -1) {
    _isAndroid = /Android/i.test(ua) ? 1 : 0;
  }
  return _isAndroid === 1;
}

if (isAndroid()) {
    const innerHeight = window.innerHeight;
    console.log('main--innerHeight', innerHeight)
    window.addEventListener('resize', () => {
        const newInnerHeight = window.innerHeight;
        console.log('main--newInnerHeight', newInnerHeight)
        if (innerHeight > newInnerHeight) {
            // 键盘弹出事件处理
            console.log("main---android 键盘弹窗事件", vm);
            vm.$store.commit('changeBtnShow', false);
            // vm.$bus.$emit('keyboardShow', false)
        } else {
            // 键盘收起事件处理
            console.log("main---android 键盘收起事件处理", vm)
            vm.$store.commit('changeBtnShow', true);
            // vm.$bus.$emit('keyboardShow', true)
        }
    });
} else if (isIOS()) {
    window.addEventListener('focusin', () => {
    // 键盘弹出事件处理
    // alert("iphone 键盘弹出事件处理")
    });
    window.addEventListener('focusout', () => {
    // 键盘收起事件处理
    // alert("iphone 键盘收起事件处理")
    });
}




const vm = new Vue({
  router,
  store,
  render: h => h(App),
  beforeCreate() {
    Vue.prototype.$bus = this;
  },
}).$mount('#app')
