<template>
  <div class="home">
    <router-view :dropdownTitle="$route.query.title" :dropdownValue="$route.query.value" :dropdownoption="$route.query.option" />
    <div id="tabbar">
      <van-tabbar v-model="active" route>
        <van-tabbar-item style="text-align: center;" replace to='/workbench'>
          <template slot-scope="props">
            <img :src="props.active ? workbench_choose : workbench" style="    margin-bottom: 0.5rem;"/>
            <div>工作台</div>
          </template>
        </van-tabbar-item>
        <van-tabbar-item style="text-align: center;" replace to='/wait-dealt'  :badge="waitDealtCount <= 0 ? '' : waitDealtCount">
          <template slot-scope="props">
            <img :src="props.active ? agency_choose : agency" style="    margin-bottom: 0.5rem;"/>
            <div>待办</div>
          </template>
        </van-tabbar-item>
        <van-tabbar-item style="text-align: center;">
          <template slot-scope="props">
            <img :src="props.active ? meter_choose : meter" style="    margin-bottom: 0.5rem;"/>
            <div>仪表盘</div>
          </template>
        </van-tabbar-item>
        <van-tabbar-item style="text-align: center;" replace to='/mine'>
          <template slot-scope="props">
            <img :src="props.active ? mine_choose : mine" style="    margin-bottom: 0.5rem;"/>
            <div>我的</div>
          </template>
        </van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      active: 0,
      agency: require('../assets/agency.svg'),
      agency_choose: require('../assets/agency_choose.svg'),
      meter: require('../assets/meter.svg'),
      meter_choose: require('../assets/meter_choose.svg'),
      mine: require('../assets/mine.svg'),
      mine_choose: require('../assets/mine_choose.svg'),
      workbench: require('../assets/workbench.svg'),
      workbench_choose: require('../assets/workbench_choose.svg'),
      waitDealtCount: 0 //  待办条数
    }
  },
  methods: {
    onChange (index) {
      // debugger
      this.active = index
      switch (index) {
        case 0:
          this.$router.push({ name: 'Workbench' })
          break
        case 1:
          this.$router.push({ name: 'WaitDealt' })
          break
        case 2:
          break
        case 3:
          this.$router.push({ name: 'mine' })
          break

        default:
          break
      }
    },
    getWaitDealtCount() {
      this.$getHttp(`${this.$baseUrl}/OS-PM/versions/${this.$store.getters.getVersionId ? this.$store.getters.getVersionId : window.localStorage.getItem('versionId')}/backlogsCount`).then(res=> {
        console.log('Home主页', res)
        if (res.status == 200) {
          this.waitDealtCount = 0;
          Object.keys(res.data).forEach(item=> {
            this.waitDealtCount = this.waitDealtCount + res.data[item]
          })
        }
      })
    }
  },
  mounted() {
    console.log('home',this.$route.query.title, this.$route.query.value)
    this.getWaitDealtCount();
    this.$bus.$on('handleVersion', ()=> {
      this.getWaitDealtCount();
    })
  }
}
</script>

