<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  created () {
    document.addEventListener('plusready', this.onPlusReady, false)
    // 扩展API加载完毕，现在可以正常调用扩展API
  },
  methods: {
    onPlusReady () {
      // 创建扫码控件来调获取手机的相机权限
      const barcode = plus.barcode.create('barcode', [plus.barcode.QR], {
        top: '-9999', // 改为-9999px隐藏该页面
        left: '0',
        width: '100%',
        height: '500px',
        position: 'static'
      })
      plus.webview.currentWebview().append(barcode)
    }
  }
}
</script>
