import axios from 'axios'
import Router from '@/router/index'
import qs from 'qs'
import { Notify } from 'vant'
axios.defaults.timeout = 60000 // 响应时间
// 配置请求头
axios.defaults.headers = {
  // 公共请求头配置
}

const sb = axios.create({
  baseURL: '',
  timeout: 6000,
  // paramsSerializer (params) {
  //   return qs.stringify(params, { arrayFormat: 'repeat' })
  // }
})


axios.defaults.baseURL = window.location.origin // 配置接口地址
// POST传参序列化(添加请求拦截器)
sb.interceptors.request.use((config) => {
  // console.log('config', config)
  // console.log(window.location.origin)
  // 在发送请求之前做某件事
  // if(config.method  === 'get'){
  //     config.data = true;
  // }

  // if(sessionStorage.getItem('token')){
  //     let token = sessionStorage.getItem('token')
  //     //已经登录
  //     config.headers['Content-Type'] = 'application/json;charset=UTF-8';
  //     config.headers['鉴权属性'] = token;
  // }
  if (config.isFormData) {  //  为了调用重审提交接口 先暂时写成这样
    console.log('表单类型12')
    // config.data = qs.stringify(config.data, { arrayFormat: 'repeat' })
    config.headers['Content-Type'] = 'application/x-www-form-urlencoded';
    config.data = qs.stringify(config.data)
    console.log('qs.stringify', config)
  }
  return config
}, (error) => {
  // console.log('错误的传参')
  return Promise.reject(error)
})

// 返回状态判断(添加响应拦截器)
sb.interceptors.response.use((res) => {
  // 对响应数据做些事
  // if(!res.data.success){
  //     return Promise.resolve(res);
  // }
  return res
}, (error) => {
  if (error.response.status === 401) {
    Router.push({ name: 'Login' })
    Notify({ type: 'warning', message: '登录已过期请重新登录', duration: 3000 })
  }
  return Promise.reject(error)
})

// 返回一个Promise(发送post请求)

// config为配置单独的header
export function fetchPost (url, params, config) {
  return new Promise((resolve, reject) => {
    sb.post(url, params, config)
      .then(response => {
        resolve(response)
      }, err => {
        reject(err)
      })
      .catch((error) => {
        reject(error)
      })
  })
}
// 返回一个Promise(发送get请求)

// config为配置单独的header
export function fetchGet (url, params, config) {
  return new Promise((resolve, reject) => {
    sb.get(url, params, config)
      .then(response => {
        resolve(response)
      }, err => {
        reject(err)
      })
      .catch((error) => {
        reject(error)
      })
  })
}

// config为配置单独的header
export function fetchPut (url, params, config) {
  console.log('fetchPut', url, 'params', params, 'config', config)
  return new Promise((resolve, reject) => {
    sb.put(url, params, config)
      .then(response => {
        resolve(response)
      }, err => {
        reject(err)
      })
      .catch((error) => {
        reject(error)
      })
  })
}

export default {
  fetchPost,
  fetchGet,
  fetchPut
}
